import React from "react";
import classes from "./Checkout.module.css";
import Burger from "../../Components/Burger/Burger";
import Axios from "../../axios-database";
import { connect } from "react-redux";
import * as AT from "../../Store/actionTypes";
class Checkout extends React.Component {
  state = {
    name: "",
    number: "",
    address: ""
  };
  placeOrder = event => {
    event.preventDefault();
    if (
      this.state.name === "" ||
      this.state.address === "" ||
      this.state.number === ""
    ) {
      alert("Please enter your data");
      return;
    }
    if (this.props.price === 0) {
      alert("Your cart is empty");
      return;
    }
    Axios.post("/orders.json", {
      userinfo: this.state,
      order: this.props.burgers,
      price: this.props.price
    })
      .then(response => {
        this.setState(state => {
          return { name: "", number: "", address: "" };
        });
        return response;
      })
      .catch(error => {
        console.log(error);
        return Promise.reject(error);
      });
    this.props.placeOrder();
  };
  onNameChange = event =>
    this.setState({
      name: event.target.value
    });
  onNumberChange = event =>
    this.setState({
      number: event.target.value
    });
  onAddressChange = event =>
    this.setState({
      address: event.target.value
    });
  render() {
    let burgers = null;
    if (this.props.burgers !== null) {
      burgers = this.props.burgers.map((value, index) => (
        <Burger
          ingredients={value}
          key={index}
          className={classes.orderedburgers}
        />
      ));
    }
    const price = this.props.price ? (
      <h3 className={classes.price}>Total Price: ${this.props.price}</h3>
    ) : (
      <h3 className={classes.price} onClick={this.props.more}>
        Add Something to Cart
      </h3>
    );
    return (
      <div className={classes.Checkout}>
        <h1>Checkout Summary</h1>
        <form className={classes.UserData}>
          <label htmlFor="name">Name: </label>
          <br />
          <input
            name="name"
            type="text"
            as="text"
            value={this.state.name}
            onChange={this.onNameChange}
          />
          <br />
          <label htmlFor="phone">Phone: </label>
          <br />
          <input
            type="tel"
            name="phone"
            value={this.state.number}
            pattern="[0-9]{3}-[0-9]{2}-[0-9]{3}"
            onChange={this.onNumberChange}
          />
          <br />
          <label htmlFor="Address">Address: </label>
          <br />
          <input
            type="address"
            name="address"
            as="text"
            value={this.state.address}
            onChange={this.onAddressChange}
          />
          <br />
          <input
            type="submit"
            value="Order Now"
            className={classes.SubmitButton}
            onClick={this.placeOrder}
          />
          {price}
        </form>
        <br />
        <div className={classes.orderedburgers}>{burgers}</div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    burgers: state.burgers,
    price: state.totalprice
  };
};
const mapDispatchToProps = dispatch => {
  return {
    placeOrder: () => dispatch(AT.placeOrder())
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Checkout);
